<template>
  <div class="list-content">
    <!-- {{val}} -->
    <van-list
        v-model="loading"
        :finished="finished"
        offset
        finished-text="没有更多了"
        @load="onLoad(val)"
        >  
            <van-cell v-for="(item,i) in list" :key="i"  @click="jinruxq(item._id)">
              <div class="content1" v-if="item.imageSrc.length>1">
                 <h3>{{item.title}}</h3>
                 <div class="content-img">
                    <div class="content-div" v-for="(item1,i) in item.imageSrc" :key="i">
                         <img :src="item1" alt=""/>
                    </div>
                 </div>
                 
              </div>
              <div class="content" v-else>
                 <h3>{{item.title}}</h3>
                 <div class="content-img" v-for="(item1,i) in item.imageSrc" :key="i">
                    <img :src="item1" alt=""/>
                 </div>
              </div>
              <div class="content-bottom">
                <p>作者{{item.author}}</p>
                <p>{{item.comment}}评论</p>
                <p>发布时间{{new Date(item.time).toLocaleDateString()}}</p>
                <div>
                    <van-icon name="cross" />
                </div> 
              </div>  
            </van-cell> 
      </van-list>  
  </div> 
</template>

<script>
import axios from 'axios'
import { Icon,Tab, Tabs,List,Cell} from 'vant';
export default {
  name:'sousuoList',
  components:{
    [Icon.name]:Icon,
    [Tab.name]:Tab,
    [Tabs.name]:Tabs,
    [List.name]:List,
    [Cell.name]:Cell,
 },
 data() {
  return {
        loading: false,
        finished: false,
        list:[],
        skip:0
  }
 },
 props:['val'],
 methods:{
  onLoad(val) {
      // 异步更新数据
      // console.log(val);
      axios({
            method:'post',
            url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/search',
            data:{
                    "key_word":val,
                    "skip":this.list.length,
                    "limit":10,
                }    
            }).then(res=>{
                this.list.push(...res.data.data)
                // this.list=res.data.data
                this.loading = false;
                console.log(res);
                console.log('list',this.list.length);
                // 数据全部加载完成
                if (this.list.length >= res.data.count){

                    this.finished = true;
                    
                  };
      })
        // console.log("123",this.skip);
        this.finished = false;
        // 加载状态结束
        

        
    },
    jinruxq(id){
      this.$router.push({
          name:'listcontent',
          params:{
            id:id
          }
      })
    },
 },
 watch:{
    val(val){
      this.list=[]
      // console.log(1,val);
      // console.log(2,this.list.length);
      // for (let index = 0; index < this.list.length; index++) {
      //     this.list.pop()
      // }
      this.onLoad(val)
      
    }
 }
}
</script>

<style lang="less" scoped>
.list-content{
  margin-bottom: 50px;
  .van-list{
    // margin-top: 105px;
  }
}
.content{
    display: flex;
    justify-content: space-between;
    .content-img{
        width: 100px;
        img{
            width: 100%;
            height:auto;
            }
    }
   
}
.content1{
    
    .content-img{
        // width: 375px;
        display: flex;
        justify-content: space-around;
        .content-div{
            width: 110px;
            img{
                width: 100%;
                height:auto;
                
            }
        }
        
    }
   
}
.content-bottom{
    display: flex;
    justify-content: space-around;
    font-size: 8px;
}
</style>