<template>
  <div>
    <form action="/" class="search_top">
        <van-search
            v-model="value"
            show-action
            autofocus
            placeholder="请输入搜索关键词"
            @search="onSearch"
            @cancel="onCancel"
            @focus="focus"
        >
        <template #left>
            <div @click="back">
                <van-icon name="arrow-left" />
            </div>
        </template>
        </van-search>
    </form>
    <div class="box_history" v-show="ishistory">
      <div class="suosou_history">
        <p>搜索历史</p>
        <van-icon name="delete-o" @click="del"/>
      </div>
      <div >
        <!-- <span >{{item}}</span> -->
        <van-cell :title="item" is-link v-for="(item,i) in arr" :key="i" class="history" @click="sousuohistory(item)">
          <template #right-icon>
            <van-icon name="cross" size='20' @click.stop="delete_history(i)"/>
          </template>
        </van-cell>
      </div>
    </div>
    
    <!-- 搜索结果 -->
     <sousuo-list :val=val  v-show="sou" class="leibiaojieguo"/>
  </div>
</template>

<script>
import sousuoList from '@/views/sousuo/sousuoList.vue'
import { Toast, Search,Icon,Cell, CellGroup  } from 'vant';
export default {
  name:'search',
  data() {
    return {
        value: '',
        arr:[],
        sou:false,
        val:'',
        ishistory:true
    }
  },
  components:{
    sousuoList,
    [Toast.name]:Toast,
    [Search.name]:Search,
    [Icon.name]:Icon,
    [Cell.name]:Cell,
    [CellGroup.name]:CellGroup
  },
  methods: {
    onSearch(value) {
      // console.log(value);
      if(value.trim()===''){
        
      }else{
        this.arr.push(value.trim())
        this.arr= [...new Set(this.arr)]
        this.val=value.trim() 
        localStorage.setItem('sousuohistory',this.arr)
        this.sou=true
        this.ishistory=false
      }
      
      
      // console.log(typeof(this.arr) );
     
    },
    onCancel() {
        this.value= ''
    },
    back(){
        this.$router.go(-1);
    },
    
    del(){
      this.arr=[]
      localStorage.setItem('sousuohistory',this.arr)
    },
    focus(){
      this.ishistory=true
      this.sou=false
    },
    sousuohistory(item){
      //  console.log(item);
      this.value=item
       this.onSearch(item)
    },
    delete_history(i){
      this.arr.splice(i,1);
      localStorage.setItem('sousuohistory',this.arr)
      // console.log(111);
    }
  },
  mounted(){
    this.arr = localStorage.getItem('sousuohistory').split(',');
    //split可将字符串分割成数组
    // this.arr = this.arr.filter(val=>val!==',') 
    // console.log(localStorage.getItem('sousuohistory').split(','));
    // console.log(localStorage.getItem('sousuohistory'));
    // console.log(this.arr);
    
  }
}
</script>

<style lang="less" scoped>
.search_top{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
}
.box_history{
  margin-top: 55px;
}
.suosou_history{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px;
  padding: 0 10px;
  p{
    font-size: 10px;
  }
}
.history{
  //  margin: 10px;
  //  font-size: 8px;
  //  span{
  //    display:inline-block;
  //    height:15px ;
  //    margin: 3px 5px;
  //    border: 1px solid rgb(172, 171, 171);
  //    border-radius: 20px;
  //    padding: 4px 6px;
  //  }
  border-bottom:1px solid #ccc ;
}
.leibiaojieguo{
  margin-top: 60px;
}
</style>